import React, { Component } from "react";
import { connect } from "react-redux";
import { timerActive } from "../action";
import { BsPauseFill, BsPlayFill, BsStopFill } from 'react-icons/bs'

class Cronometer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      minutes: 0,
      seconds: 0,
      miliseconds: 0
    }
  }

  start = () => {
    const { timerActive } = this.props
    timerActive(true)
    this.cronometer = setInterval(() => {
      const { minutes, seconds, miliseconds } = this.state;
      let newMilisecond = miliseconds + 1;
      let newSeconds = seconds;
      let newMinutes = minutes;

      if (newMilisecond >= 100) {
        newMilisecond = 0;
        newSeconds += 1;
      }

      if (newSeconds >= 60) {
        newSeconds = 0;
        newMinutes += 1;
      }

      document
        .title = `${'00'.slice(`${newMinutes}`.length) + newMinutes}:${'00'.slice(`${newSeconds}`.length) + newSeconds} - Cronometer` 
      
      this.setState({ miliseconds: newMilisecond, seconds: newSeconds, minutes: newMinutes })

    }, 10)
  }

  pause = () => {
    const { timerActive } = this.props
    timerActive(false)
    clearInterval(this.cronometer)
  }

  stop = () => {
    const { timerActive } = this.props
    timerActive(false)
    clearInterval(this.cronometer)
    document.title = '00:00 - Cronometer';
    this.setState({ minutes: 0, seconds: 0, miliseconds: 0 })
  }

  render() {
    const { isTimerActive } = this.props;
    const { minutes, seconds, miliseconds } = this.state;
    console.log(isTimerActive);
    return (
      <div style={{ animation: 'aparecerCronometer 1s ease', position: 'relative' }} className="timer-container">
        <h1 className="hero-title">Cronometer</h1>
        <div className="time-container">
          <h1>
            {`${'00'.slice(`${minutes}`.length) + minutes}:${'00'.slice(`${seconds}`.length) + seconds}`}
          </h1>
          <h2>{`${'00'.slice(`${miliseconds}`.length) + miliseconds}`}</h2>
        </div>
        <div className="control-painel">
          { isTimerActive 
          ? 
            <button
              onClick={ this.pause }
            >
              <BsPauseFill />
            </button>
          :
            <button
              onClick={ this.start }
            >
              <BsPlayFill />
            </button>
          }
          <button
            onClick={ this.stop }
          >
            <BsStopFill />
          </button>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ timeReducer: { isTimerActive } }) => ({ isTimerActive })

const mapDispatchToProps = (dispatch) => ({
  timerActive: (bool) => dispatch(timerActive(bool))
});

export default connect(mapStateToProps, mapDispatchToProps)(Cronometer)