import React, { useState } from 'react'
import { useTransition, animated } from 'react-spring';
import { connect } from 'react-redux';
import { FaAngleRight, FaAngleLeft } from 'react-icons/fa';
import MusicsPlaylist from './components/MusicsPlaylist';
import MusicPanel from './components/MusicPanel';
import TopButtons from './components/TopButtons';
import Cronometer from './components/Cronometer';
import Timer from './components/Timer';
import Alarm from './songs/musicas/alarm.mp3'


const App = ({ isTimerActive, alarm, muted }) => {
  const [mode, setMode] = useState('Timer');

  const timerTransition = useTransition(mode === 'Timer', {
    from: { left: -500, opacity: 0 },
    enter: { left: 480, opacity: 1 },
    leave: { left: -500, opacity: 0 }
  })

  const cronometerTransition = useTransition(mode !== 'Timer', {
    from: { right: -500, opacity: 0 },
    enter: { right: 480, opacity: 1 },
    leave: { right: -500, opacity: 0 }
  })

  const toggleMode = () => {
    if (mode === 'Timer') {
      setMode('Cronometer')
    } else {
      setMode('Timer')
    }
  }

  return (
    <>
      { alarm && <audio src={ Alarm } autoPlay /> }
      { (isTimerActive && !muted) && 
        <MusicsPlaylist /> 
      }
      <div className="total-container" >
        <TopButtons />
        <MusicPanel />
        { timerTransition((style, item) =>
        item &&
          <animated.div className="mode" style={ style }>
            <Timer />
          </animated.div>
        )
        }
        { cronometerTransition((style, item) =>
          item &&
          <animated.div className="mode" style={ style }>
            <Cronometer />
          </animated.div>
        )
        }
      </div>

      { mode === 'Timer'
      
      ? <button
          disabled={ isTimerActive }
          className="next"
          onClick={ toggleMode }
        >
          <FaAngleRight />
        </button>

      : <button
          disabled={ isTimerActive }
          className="prev"
          onClick={ toggleMode }
        >
          <FaAngleLeft />
        </button>

      }
      
    </>
  );
  
}

const mapStateToProps = (
  { timeReducer: { isTimerActive }, controlReducer: { alarm, muted } }
) => ({ isTimerActive, alarm, muted })

export default connect(mapStateToProps, null)(App);
